* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

p {
  margin: 0;
}

img {
  display: block;
}

.is-hidden {
  display: none;
}

.breed-select {
  margin-bottom: 30px;
}

.cat-info {
  max-width: 600px;
  border: 1px double rgba(88, 151, 251, .5);
  border-radius: 20px;
  padding: 20px 40px;
  box-shadow: 3px 2px 17px 4px rgba(88, 151, 251, .5), -3px -2px 17px 4px rgba(88, 151, 251, .5);
}

.cat_image-wrap {
  width: 50%;
  height: 25%;
  border-radius: 10px;
  margin: 0% 25%;
}

.cat-pic {
  max-width: 100%;
  max-height: auto;
}

.cat_information {
  justify-content: center;
  display: grid;
}

.error {
  color: #d8000c;
  background-color: #ffbaba;
  background-image: url("https://i.imgur.com/GnyDvKN.png");
  background-position: 10px;
  background-repeat: no-repeat;
  border: 1px solid;
  margin: 10px 0;
  padding: 15px 10px 15px 50px;
}

.ss-main {
  max-width: 300px;
  min-height: 40px;
}

.ss-content .ss-list .ss-option:hover {
  background-color: rgba(88, 151, 251, .5);
}

.loader-wrap {
  align-items: center;
  gap: 20px;
  display: flex;
}

.loader-text {
  font-size: 26px;
  font-weight: 600;
}

.loader {
  width: 100px;
  height: 100px;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #5897fb;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

